import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer } from 'preact-i18n';
import linkState from 'linkstate';

import InboxUser from '../../components/inbox/users';

import mu from '../../lib/musher-util';
const MARGIN_TOP = mu.marginTop(false);
const MARGIN_BOTTOM = mu.marginBottom();

const initialState = {
    isLoading: false,
	limit: 25,
	offset: 0,
    search: '',
};

@observer
class Inbox extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
    }

    async loadMessages({ limit, offset, search }) {
		this.setState({ isLoading: true });
		const { messageStore, userStore } = this.props.stores;
        await userStore.getNotifications();
        await messageStore.getUnReadMessages();
		await messageStore.loadConversationList({ limit, offset, search });
		this.setState({ isLoading: false, searchText: search });
	}

    searchForMessages = () => {
        const { search, limit } = this.state;
		this.setState({
			offset: 0,
		});
		this.loadMessages({ search, limit });
    }

    componentDidMount() {
		this.loadMessages({});
	}

	// componentWillReceiveProps(nextProps) {
	// 	this.loadMessages(nextProps);
	// }

    render() {
        const { search, searchText } = this.state;

        const { messageStore } = this.props.stores;
        const { users } = messageStore;


        return (
			<div class='container-fluid' style={`margin-bottom: ${MARGIN_BOTTOM}; margin-top: ${MARGIN_TOP};`}>

                <div class='row'>
                    <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-3'>
                        <a href='/inbox/new' class='float-right' style='font-size: 1.5em; line-height: 1.0em;'><i class='fas fa-edit' /></a>
                        <h5><Text id='inbox.title'>Inbox</Text></h5>
                    </div>
                    <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-2'>
                        <div class='d-flex flex-row flex-nowrap'>
                            <div class='flex-grow-1'>
                                <label class='sr-only' for='inboxSearch'><Text id='inbox.search'>Search</Text></label>
                                <Localizer>
                                    <input
                                        type='text'
                                        class='form-control mb-2'
                                        id='inboxSearch'
                                        onInput={linkState(this, 'search')}
                                        value={search}
                                    />
                                </Localizer>
                            </div>
                            <div class='ml-1'>
                                <button
                                    type='button'
                                    class='btn btn-primary mb-2'
                                    onClick={this.searchForMessages}
                                ><Text id='inbox.search-button'>Search</Text></button>
                            </div>
                        </div>
                    </div>
				</div>

                {searchText && <>
                    <div class='row'>
                        <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-3'>
                            <span class='text-muted'>
                                <Text id='inbox.search-for'>Searched for</Text>: {searchText}
                            </span>
                        </div>
                    </div>
                </>}
                <div class='row'>
                    {users && users.map(user => <>
                        <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-3'>
                            <InboxUser stores={this.props.stores} user={user} />
                        </div>
                    </>)}
                </div>
                <div class='row mt-5'>
                    <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-2'>
                        <a href='/inbox/new' class='btn btn-primary btn-block' style=''><i class='fas fa-edit' /> <Text id='inbox.new'>New message</Text></a>
                    </div>
                </div>
            </div>
        );
    }
}

export default Inbox;
